import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import PageTitle from '../components/PageTitle';

const ForgotPass = () => {
    const [num, setNum] = useState(0);
    return (
        <div className='forgot-area'>
            <PageTitle>Sorry but forn now we can't do nothing about that.</PageTitle>
            <img onClick={() => {
                if (num < 5) {
                    setNum(num + 1);
                } else {
                    if (num >= 5) {
                        setNum(0);
                    }
                }
            }} src='https://i.giphy.com/media/yxF4HmDIXw83S/200.webp' alt='yes you forgot and you are helpless for now' />
            { num < 5 ? null : (<p>You can try this link <a rel="noopener noreferrer" href="https://kanbanx.com/#/usuario/esqueceu-senha" target="_blank" >
                link here :)
            </a></p>)}
            <p>Or back to login page <Link to="/">Login</Link></p>
        </div>
    );
}

export default ForgotPass;