import React, { useEffect, useState } from "react";

export default (props) => {
  const [value, setValue] = useState(props.value);
  useEffect(() => {
    setValue(props.value);
  }, [props.value]);
  return props.debug === true ? (
    <input
      {...props}
      value={value}
      onChange={(e, ...args) => {
        if (props.onChange) {
          props.onChange(e, ...args);
        }
        setValue(e.target.value);
      }}
    />
  ) : null;
};
