import React, { useEffect, useState } from "react";
import PageTitle from "../components/PageTitle";
import ResultArea from "../components/ResultArea";
import md5 from "../utils/md5";
import { sha512 } from "js-sha512";

export default () => {
  const [result, setResult] = useState("");
  const [username, setUsername] = useState("admin");
  const [encrypter, setEncrypter] = useState("md5");
  const [pwd, setPwd] = useState("admin");
  const [pwdEncrypted, setPwdEncrypted] = useState(md5("admin"));

  useEffect(() => {
    setResult(
      `xmo-client -p "Device/UserAccounts/Users/User[Login='${username}']/Password" -s "${pwdEncrypted}"`
    );
  }, [username, pwdEncrypted]);

  useEffect(() => {
    encryptPass(encrypter);
  }, [pwd]);

  const getBtnClass = (current, active) => {
    if (current === active) {
      return "btn btn-sm btn-primary";
    }
    return "btn btn-sm btn-secondary";
  };
  const encryptPass = (type) => {
    if (type === "sha-512") {
      setPwdEncrypted(sha512(pwd));
    } else {
      setPwdEncrypted(md5(pwd));
    }
  };

  return (
    <div className="row">
      <div className="col-12">
        <PageTitle>Change password</PageTitle>
        <div className="row">
          <div className="col-12 encrypt-area">
            <button
              className={getBtnClass("md5", encrypter)}
              onClick={() => {
                setEncrypter("md5");
                encryptPass("md5");
              }}
            >
              MD5
            </button>
            <button
              className={getBtnClass("sha-512", encrypter)}
              onClick={() => {
                setEncrypter("sha-512");
                encryptPass("sha-512");
              }}
            >
              SHA-512
            </button>
          </div>
        </div>
        <div className="form-row mb-3 text-left">
          <div className="col-12">
            <label>Username</label>
            <input
              id="username"
              name="username"
              type="text"
              className="form-control"
              value={username}
              onChange={(e) => {
                setUsername(e.target.value);
              }}
            />
          </div>
        </div>
        <div className="form-row mb-3 text-left">
          <div className="col-12">
            <label>Password</label>
            <input
              id="password"
              name="password"
              type="text"
              className="form-control"
              value={pwd}
              onChange={(e) => {
                setPwd(e.target.value);
              }}
            />
          </div>
        </div>
        <ResultArea result={result} debug="true" />
      </div>
    </div>
  );
};
