import React, { useContext } from "react";
import { Route } from "react-router-dom";
import Header from "../components/Header";
import UbootParser from "./UbootParser";
import Dload from "./Dload";
import Sidemenu from "../components/Sidemenu";
import Md5Page from "./Md5Page";
import ChangePass from "./ChangePass";
import PrivateRoute from '../components/PrivateRoute';
import { AuthContext } from '../contexts/Auth.context';


export default () => {
  const { handleLogout } = useContext(AuthContext);
  const menuItems = [
    {
      path: "/",
      component: UbootParser,
      label: "Uboot",
      exact: true,
      private: true
    },
    {
      path: "/dload",
      component: Dload,
      label: "Dload",
      exact: true,
      private: true
    },
    {
      path: "/md5",
      component: Md5Page,
      label: "MD5",
      exact: true,
      private: true
    },
    {
      path: "/change-pass",
      component: ChangePass,
      label: "Change pass",
      exact: true,
      private: true
    },
    {
      label: 'Logout',
      action: () => {
        handleLogout();
      }
    }
  ];
  const renderRoutes = (pg, i) => {
    if (!!pg.path) {
      return pg.private ? <PrivateRoute {...pg} key={i + 1} />: <Route {...pg} key={i + 1} />;
    }
    return null;
  };
  return (
    <div className="App">
      <div className="container">
          <div className="row">
            <Header />
          </div>
          <div className="row">
            <div className="col-3">
              <Sidemenu pages={menuItems}></Sidemenu>
            </div>
            <div className="col-8">
                {menuItems.map(renderRoutes)}
            </div>
          </div>
      </div>
    </div>
  );
};
