import React, { useEffect, useState } from "react";

export default ({ ipAddress, onDataChange }) => {
  const [ipInfo, setIpInfo] = useState((ipAddress || "...").split("."));
  useEffect(() => {
    const ip = ipAddress || "...";
    setIpInfo(ip.split("."));
  }, [ipAddress]);
  const setOctet = (e, inputNum) => {
    let ipInfoTmp = [...ipInfo];
    if (/^\d{1,3}$/.test(e.target.value) || e.target.value === "") {
      ipInfoTmp[inputNum] = e.target.value;
      setIpInfo(ipInfoTmp);
      if (onDataChange) {
        onDataChange(ipInfoTmp.join("."));
      }
    }
  };
  return (
    <div className="ip-input">
      <input
        className="form-control"
        type="text"
        name="ip1"
        value={ipInfo[0]}
        maxLength={3}
        onChange={(e) => {
          setOctet(e, 0);
        }}
      />
      <span>.</span>
      <input
        className="form-control"
        type="text"
        name="ip2"
        value={ipInfo[1]}
        maxLength={3}
        onChange={(e) => {
          setOctet(e, 1);
        }}
      />
      <span>.</span>
      <input
        className="form-control"
        type="text"
        name="ip3"
        value={ipInfo[2]}
        maxLength={3}
        onChange={(e) => {
          setOctet(e, 2);
        }}
      />
      <span>.</span>
      <input
        className="form-control"
        type="text"
        name="ip4"
        value={ipInfo[3]}
        maxLength={3}
        onChange={(e) => {
          setOctet(e, 3);
        }}
      />
    </div>
  );
};
