import React, { useEffect, useState } from "react";
import IPInput from "../components/IPInput";
import PageTitle from "../components/PageTitle";
import ResultArea from "../components/ResultArea";

export default () => {
  const [dloadCmd, setDloadCmd] = useState("");
  const [ipAddress, setIpAddress] = useState("192.168.0.1");
  const [selectedTemplate, setSelectedTemplate] = useState("192.168.0.10");
  const ipTemplates = [
    "192.168.0.10",
    "192.168.1.10",
    "192.168.2.10",
    "10.10.10.10"
  ];

  const [fwFile, setFwFile] = useState(
    "FAST3890V3_SSC_EXT_sw18.76.10.9x-TEST_monolithic.bin"
  );
  const handleChange = (e, setter) => {
    setter(e.target.value);
  };
  useEffect(() => {
    setDloadCmd(`dload -a1 ${ipAddress}:${fwFile}`);
  }, [ipAddress, fwFile]);
  return (
    <>
      <PageTitle>Dload command</PageTitle>
      <form>
        <div className="row">
          <div className="col-12">
            <div className="form-group">
              <label>Ip template</label>
              <select
                style={{ float: "left" }}
                value={selectedTemplate}
                key={selectedTemplate}
                onChange={(e) => {
                  setSelectedTemplate(e.target.value);
                  if (e.target.value) {
                    setIpAddress(e.target.value);
                  }
                }}
              >
                <option value="">None</option>
                {ipTemplates.map((ip) => (
                  <option key={ip}>{ip}</option>
                ))}
              </select>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <div className="form-group">
              <label>Ip address:</label>
              <IPInput
                ipAddress={ipAddress}
                onChange={(newIp) => {
                  setIpAddress(newIp);
                }}
              />
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <div className="form-group">
              <label>Filename:</label>
              <input
                placeholder="filename"
                onChange={(e) => handleChange(e, setFwFile)}
                className="form-control"
                type="text"
                value={fwFile}
              />
            </div>
          </div>
        </div>
      </form>
      <ResultArea result={dloadCmd} />
    </>
  );
};
