import React, { useEffect, useState } from "react";
import DebugInput from "./DebugInput";

export default ({ result, alertPosition, debug }) => {
  alertPosition = alertPosition || "bottom";
  let hiddenField = {};
  const [resultValue, setResultValue] = useState("");
  const [message, setMessage] = useState("");
  useEffect(() => {
    setResultValue(result);
  }, [result]);
  return (
    <div className="row">
      <div className="col-12">
        {!!message && alertPosition === "top" && (
          <div className="alert alert-success mb-2">{message}</div>
        )}
        <div className="result-container">
          <div className="result">{resultValue}</div>
          <input
            className="hidden-field"
            ref={(hf) => (hiddenField = hf)}
            type="text"
            onChange={() => {}}
            value={resultValue}
          />
          <button
            className="btn btn-secondary"
            onClick={() => {
              hiddenField.select();
              document.execCommand("copy");
              setMessage("Copied!");
              setTimeout(() => {
                setMessage("");
              }, 2000);
            }}
          >
            Copy
          </button>
        </div>
        {!!message && alertPosition === "bottom" && (
          <div className="alert alert-success mt-2">{message}</div>
        )}
        <DebugInput debug={debug} type="text" className="form-control" />
      </div>
    </div>
  );
};
