import React, { useState } from "react";
import md5 from "../utils/md5";

export default ({ id }) => {
  // const loadData = () => {
  //   let tmpVal = localStorage.getItem("md5-samples");
  //   let data = ["", "admin", "expert", "internal"];
  //   if (!!tmpVal) {
  //     data = JSON.parse(tmpVal);
  //   }
  //   console.log("data", data);
  //   return data;
  // };

  // const saveData = (samples) => {
  //   localStorage.setItem("md5-samples", JSON.stringify(samples));
  // };

  const [samples, setSamples] = useState(["", "admin", "expert", "internal"]);
  const [newSample, setNewSample] = useState("");

  function handleAddClick() {
    if (samples.find((s) => s === newSample) !== null) {
      setSamples([...samples, newSample]);
    }
  }
  const renderMd5 = (val) => {
    return (
      <b key={val === "" ? "empty" : val}>
        {val === "" ? "empty" : val} =&gt; {md5(val)}
        <br />
      </b>
    );
  };
  return (
    <div>
      {samples.map(renderMd5)}
      <input
        type="text"
        name="newSample"
        onChange={(e) => {
          setNewSample(e.target.value);
        }}
      />
      <button onClick={handleAddClick}>Add</button>
    </div>
  );
};
