import React from "react";
import { Link } from "react-router-dom";

export default ({ pages }) => {

  const renderMenuItem = (pg, i) => {
    if (pg.action) {
      return <p className='nav-link action' onClick={pg.action}>{pg.label}</p>;
    }
    if (!!pg.path) {
      return <Link className="nav-link" key={i + 1} to={pg.path}> {pg.label ? pg.label : pg.path}</Link>;
    }
  };

  const renderPages = () => {
    return pages.map((p, i) => {
      return p.visible === false ? null : (
        <li className="nav-item" key={i + 1}>
          {renderMenuItem(p, i)}
        </li>
      );
    });
  };
  return (
    <>
      <ul className="nav nav-pills flex-column sidebar">{renderPages()}</ul>
    </>
  );
};
