import React, { useState } from "react";
import PageTitle from "../components/PageTitle";

const hasVarExp = /\$\{([a-z0-9_-]+)\}/i;

export default () => {
  const [text, setText] = useState("");
  const [variablesList, setVariablesList] = useState([]);
  const handleParse = () => {
    const variables = {},
      variablesTmp = [];
    const dataParsed = text.split("\n");
    dataParsed.forEach((line) => {
      const [varname, val] = line.split("=");
      variables[varname] = {
        name: varname,
        val: val,
        origVal: val,
        containsVars: hasVarExp.test(val)
      };
      variablesTmp.push(variables[varname]);
    });
    variablesTmp.forEach((vr) => {
      if (vr.containsVars) {
        const result = /\$\{([a-z0-9_-]+)\}/gi.exec(vr.val);
        const varList = result.splice(1, result.length);
        varList.forEach((v) => {
          if (!!variables[v]) {
            vr.val = vr.val.replace("${" + v + "}", variables[v].val);
          }
        });
      }
    });
    setVariablesList(variablesTmp.sort((v1, v2) => v2.name - v1.name));
  };
  const views = {
    inputArea: () => {
      return (
        <>
          <textarea
            className="input-area"
            onChange={(e) => {
              setText(e.target.value);
            }}
            value={text}
          ></textarea>
        </>
      );
    },
    parsedArea: () => {
      return (
        <>
          <table className="table table-striped table-bordered table-cmd">
            <thead>
              <tr>
                <th>Variavel</th>
                <th>Valor</th>
              </tr>
              <tr>
                <th colSpan="2">
                  <input className="form-control" type="text" />
                </th>
              </tr>
            </thead>
            <tbody>
              {variablesList.map((vrl) => {
                return (
                  <tr key={vrl.name}>
                    <td>{vrl.name}</td>
                    <td>{vrl.val}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </>
      );
    }
  };
  return (
    <div className="row">
      <div className="col-12">
        <PageTitle>Uboot</PageTitle>
        {variablesList.length ? <views.parsedArea /> : views.inputArea()}
        <div className="row">
          <div className="col-8"></div>
          <div className="col-4 text-right">
            {!variablesList.length && (
              <button
                className="btn btn-primary btn-block btn-parse"
                onClick={handleParse}
              >
                Parse
              </button>
            )}
            {variablesList.length > 0 && (
              <button
                className="btn btn-primary btn-block btn-parse"
                onClick={() => {
                  setVariablesList([]);
                }}
              >
                Back
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};
